<template>
  <div id="webinar">
    <div class="container my-4">
      <!-- List  -->
      <div
        class=""
        v-if="
          menu.menu == 'my-activity' &&
          menu.submenu == 'webinar' &&
          menu.key == ''
        "
      >
        <div class="row">
          <div class="col-12">
            <div class="ps-3 pointer" style="top: 100px" @click="redirect">
              <h5 class="d-inline">Webinars</h5>
            </div>
          </div>
        </div>

        <!-- Tabs  -->
        <div class="row mt-3">
          <div class="col-md-12 col-12 menu-tab">
            <button
              v-for="i in category"
              :key="i"
              class="btn-mentoring btn-sm mx-1"
              :class="tab == i.dtl_category ? 'btn-type-1' : 'btn-type-2'"
              @click="checkCategory(i.dtl_category)"
              style="text-transform: capitalize"
            >
              {{ $customText.removeDash(i.dtl_category) }}
            </button>
          </div>
        </div>

        <!-- Content  -->
        <div class="row mt-2">
          <div class="col">
            <div class="card border-0 shadow-sm">
              <div class="card-body">
                <!-- Career Industry  -->
                <div>
                  <Splide :options="options">
                    <SplideSlide v-for="i in webinar_data" :key="i">
                      <div class="frame m-0 p-0">
                        <iframe
                          class="p-0 m-0"
                          lazy="loading"
                          width="100%"
                          height="100%"
                          :src="i.dtl_video_link"
                          frameborder="1"
                          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                          allowfullscreen
                        ></iframe>
                        <div class="frame-overlay" @click="webinar(i.id)">
                          <div
                            class="
                              d-flex
                              align-items-center
                              justify-content-center
                              w-100
                              h-100
                            "
                          >
                            {{ i.dtl_name }}
                          </div>
                        </div>
                      </div>
                    </SplideSlide>
                  </Splide>
                </div>

                <!-- History -->
                <div class="mt-3">
                  <b class="text-primary">Webinar History</b>
                  <hr class="my-1" />
                  <!-- Empty  -->
                  <div class="row" v-if="webinar_history.data?.length == 0">
                    <div class="col py-4 text-center">
                      <span class="text-muted">
                        No history of watching webinars.</span
                      >
                    </div>
                  </div>
                  <div v-if="webinar_history.data?.length != 0">
                    <div class="table-responsive">
                      <table class="table align-middle">
                        <thead>
                          <tr class="text-center">
                            <th>No</th>
                            <th>Topic</th>
                            <th>Category</th>
                            <th>Date & Time</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr class="text-center">
                            <td>1</td>
                            <td>Career Meetup 101</td>
                            <td>Career Exploration</td>
                            <td>
                              <small>
                                20 Feburary 2022 <br />
                                14.00 WIB
                              </small>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>

                    <!-- Pagination  -->
                    <nav class="mt-3" v-if="webinar_history.from != null">
                      <ul class="pagination justify-content-center">
                        <li
                          class="page-item"
                          v-if="webinar_history.current_page != 1"
                        >
                          <a
                            class="page-link"
                            @click="getPage(webinar_history.links[0].url)"
                          >
                            <i class="fa-solid fa-chevron-left"></i>
                          </a>
                        </li>
                        <div
                          v-for="(i, index) in webinar_history.last_page"
                          :key="index"
                        >
                          <li
                            class="page-item"
                            v-if="
                              webinar_history.current_page - 2 < i &&
                              webinar_history.current_page + 2 > i
                            "
                          >
                            <a
                              class="page-link"
                              :class="
                                webinar_history.current_page == i
                                  ? 'bg-primary text-white'
                                  : ''
                              "
                              href="#"
                              @click="
                                getPage(webinar_history.path + '?page=' + i)
                              "
                              >{{ i }}</a
                            >
                          </li>
                        </div>
                        <li
                          class="page-item"
                          v-if="
                            webinar_history.current_page !=
                            webinar_history.last_page
                          "
                        >
                          <a
                            class="page-link"
                            @click="getPage(webinar_history.next_page_url)"
                          >
                            <i class="fa-solid fa-chevron-right"></i>
                          </a>
                        </li>
                      </ul>
                    </nav>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!-- Detail  -->
      <div class="row">
        <div class="col">
          <v-view
            :menu="menu"
            v-if="
              menu.menu == 'my-activity' &&
              menu.submenu == 'webinar' &&
              menu.key != ''
            "
          ></v-view>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import View from "@/components/student/page/activity/webinar-detail/view";

export default {
  name: "webinar",
  components: {
    "v-view": View,
  },
  data() {
    return {
      tab: "career",
      menu: {
        menu: "",
        submenu: "",
        key: "",
        key2: "",
      },
      category: [],
      webinar_data: [],
      webinar_history: [],
      options: {
        // autoPlay: true,
        // type: "loop",
        focus: "left",
        arrows: false,
        pagination: true,
        width: "100%",
        perPage: 2,
        gap: 20,
        padding: { left: "0", right: "1rem" },
        breakpoints: {
          800: {
            perPage: 2,
          },
          640: {
            perPage: 1,
            padding: { left: "0", right: "0" },
          },
        },
      },
    };
  },
  methods: {
    webinar(i) {
      this.$router.push({ path: "/user/my-activity/webinar/" + i });
    },

    async getCategory() {
      try {
        const response = await this.$axios.get(
          "student/list/webinar/categories"
        );

        this.category = response.data.data;
        // console.log(response.data);

        if (response.data.data && response.data.data.length != 0) {
          let category = response.data.data[0].dtl_category;
          this.tab = category;
          this.getData(category);
        }
      } catch (e) {
        console.log(e.response);
      }
    },

    checkCategory(i) {
      this.tab = i;
      this.getData(i);
    },

    async getData(i) {
      try {
        const response = await this.$axios.get(
          "student/programme/detail/webinar/" + i
        );

        this.webinar_data = response.data.data;
        // console.log(response.data);
      } catch (e) {
        console.log(e.response);
      }
    },

    async getHistory() {
      try {
        const response = await this.$axios.get(
          "student/list/activities/webinar"
        );

        this.webinar_history = response.data.data;
        // console.log(response.data);
      } catch (e) {
        console.log(e.response);
      }
    },
  },
  watch: {
    $route(to) {
      this.menu.menu = to.params.menu;
      this.menu.submenu = to.params.submenu;
      this.menu.key = to.params.key;
      this.menu.key2 = to.params.key2;
    },
  },
  created() {
    this.menu.menu = this.$route.params.menu;
    this.menu.submenu = this.$route.params.submenu;
    this.menu.key = this.$route.params.key;
    this.menu.key2 = this.$route.params.key2;

    this.getCategory();
    this.getHistory();
  },
};
</script>
<style scoped>
.frame {
  position: relative;
  overflow: hidden;
  width: 100% !important;
  height: 240px;
  border-radius: 10px;
  border: none;
  box-shadow: 10px 10px 17px -10px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 10px 10px 17px -10px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 10px 17px -10px rgba(0, 0, 0, 0.75);
  transition: all 0.3s ease-in-out;
}

iframe[lazy="loading"] {
  background-image: url("~@/assets/img/loading.gif");
  background-size: 8%;
  background-repeat: no-repeat;
  background-position: center;
}

.frame-overlay {
  border-radius: 10px;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  background: #1a2d63d5;
  opacity: 0;
  font-size: 18px;
  font-weight: bold;
  color: #fff;
  cursor: pointer;
  transition: all 0.4s ease-in;
}

.frame:hover .frame-overlay {
  opacity: 1;
}
</style>